.agmes_backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.agmes_modal {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
  width: 90%;
  max-width: 860px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: agmes_fadein 0.25s ease;
}

@keyframes agmes_fadein {
  from {
    opacity: 0;
    transform: scale(0.96);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* ---------- HEADER ---------- */
.agmes_header {
  background: var(--gradient-primary, linear-gradient(135deg, #64748b, #334155));
  color: #fff;
  padding: 14px 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.agmes_header h3 {
  font-size: 1.1rem;
  margin: 0;
}

.agmes_close {
  background: none;
  border: none;
  color: #fff;
  font-size: 1.2rem;
  cursor: pointer;
  transition: 0.2s;
  appearance:none; border:0; background:transparent; color:#fff;
  width:36px; height:36px; border-radius:999px; display:grid; place-items:center;
  cursor:pointer; transition:transform .15s ease, background .15s ease;
}

.agmes_close:hover {
background:rgba(255,255,255,.12); transform:rotate(90deg);

}

/* ---------- BODY ---------- */
.agmes_body {
  padding: 16px 20px;
  background: #fafafa;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.agmes_search {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 6px 10px;
  margin-bottom: 10px;
}

.agmes_search svg {
  color: #888;
}

.agmes_search input {
  border: none;
  outline: none;
  flex: 1;
  font-size: 0.95rem;
  background: transparent;
}

/* ---------- TABLE (con CSS GRID) ---------- */
.agmes_table {
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  overflow: hidden;
  background: #fff;
  max-height: 420px;
  overflow-y: auto;
}

/* Cada fila (header y rows) es un grid de 5 columnas */
.agmes_headerrow,
.agmes_row {
  display: grid;
  grid-template-columns:
    0.7fr   /* N Mesa */
    2fr     /* Materia */
    2.2fr     /* Docentes */
    1fr   /* Elegible */
    1fr;  /* Accin */
  align-items: center;
  border-bottom: 1px solid #e9e9e9;
}

/* Header estilos generales */
.agmes_headerrow {
  background: #f1f3f4;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 0.85rem;
}

.agmes_row:hover {
  background: #f9fbff;
}

/* Cada celda */
.agmes_column {
  padding: 8px 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start; /* base por defecto */
}

/* Columnas centradas: N Mesa, Elegible y Accin */
.agmes_column.num,
.agmes_column.elegible,
.agmes_column.accion {
  justify-content: center;
  text-align: center;
}

/* Columnas pegadas a la derecha: Materia y Docentes */
.agmes_column.materia,
.agmes_column.docentes {
  text-align: right;
}

/* ---------- ICON BUTTON ---------- */
.agmes_iconbtn {
  border: none;
  background: #2ecc71;
  color: #fff;
  border-radius: 6px;
  padding: 6px 9px;
  cursor: pointer;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.agmes_iconbtn:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.agmes_iconbtn:hover:not(:disabled) {
  background: #27ae60;
}

/* ---------- EMPTY STATE ---------- */
.agmes_nodata {
  text-align: center;
  padding: 40px 10px;
  color: #777;
  font-size: 0.95rem;
}

/* ---------- FOOTER ---------- */
.agmes_footer {
  background: #f1f1f1;
  padding: 10px 16px;
  display: flex;
  justify-content: flex-end;
  border-top: 1px solid #ddd;
}

.agmes_btnclose {
  background: #34495e;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 8px 14px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: 0.2s;
}

.agmes_btnclose:hover {
  background: var(--primary-600, #2563eb);
}
/* Base: por defecto alineamos a la izquierda */
.agmes_column {
  padding: 8px 10px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start; /* base */
}

/* Columnas centradas: N Mesa, Elegible y Accin */
.agmes_column.num,
.agmes_column.elegible,
.agmes_column.accion {
  justify-content: center;
  text-align: center;
}

/* Columnas pegadas a la derecha: Materia y Docentes */
.agmes_column.materia,
.agmes_column.docentes {

  text-align: right;
}
/* ---------- TABS TIPO BARRA (ESTILO CAPTURA) ---------- */
.agmes_tabs {
  display: flex;
  align-items: flex-end;
  gap: 32px;
  padding: 10px 20px 0;
  background: #ffffff;
  border-bottom: 1px solid #e5e7eb;
}

/* Botn de tab: texto plano */
.agmes_tab {
  position: relative;
  background: transparent;
  border: none;
  padding: 0 0 10px;
  font-size: 0.95rem;
  font-weight: 500;
  color: #4b5563;           /* gris azulado para las inactivas */
  cursor: pointer;
  transition:
    color 0.15s ease,
    opacity 0.15s ease;
}

/* Hover en tab inactiva */
.agmes_tab:hover {
  color: #1f2937;
  opacity: 0.9;
}

/* Tab activa: texto azul + subrayado gordito */
.agmes_tab.agmes_tab--active {
  color: #0b3c82;           /* azul fuerte tipo institucional */
  font-weight: 600;
}

/* Lnea azul debajo de la tab activa */
.agmes_tab.agmes_tab--active::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
  height: 3px;
  border-radius: 999px;
  background: #0b63d1;      /* barrita azul */
}

/* Responsive: que no explote en pantallas chicas */
@media (max-width: 600px) {
  .agmes_tabs {
    gap: 16px;
    padding: 8px 12px 0;
  }

  .agmes_tab {
    font-size: 0.85rem;
    padding-bottom: 8px;
  }
}
.agmes_column{
  padding: 8px 10px 8px 0;
}