/* =========================================================
   ModalInfoPrevia.css  Tema AZUL (aliasando reg-* a blue-inst-*)
   ========================================================= */
:root{
  /* ====== Paleta base (alias a institucional azul) ====== */
  --reg-primary:             var(--blue-inst-primary);
  --reg-primary-light:       var(--blue-inst-primary-light);
  --reg-primary-dark:        var(--blue-inst-primary-dark);

  --reg-secondary:           var(--blue-inst-secondary);
  --reg-accent:              var(--blue-inst-accent);

  --reg-success:             var(--blue-inst-success);
  --reg-warning:             var(--blue-inst-warning);
  --reg-danger:              var(--blue-inst-danger);

  --reg-light:               var(--blue-inst-light);
  --reg-dark:                var(--blue-inst-dark);

  --reg-gray-100:            var(--blue-inst-gray-100);
  --reg-gray-200:            var(--blue-inst-gray-200);
  --reg-gray-300:            var(--blue-inst-gray-300);
  --reg-gray-400:            var(--blue-inst-gray-400);
  --reg-gray-500:            var(--blue-inst-gray-500);
  --reg-gray-600:            var(--blue-inst-gray-600);
  --reg-gray-700:            var(--blue-inst-gray-700);
  --reg-gray-800:            var(--blue-inst-gray-800);
  --reg-gray-900:            var(--blue-inst-gray-900);

  /* Radios / transicin / sombras (alias) */
  --reg-border-radius:       var(--blue-inst-radius, 10px);
  --reg-border-radius-lg:    var(--blue-inst-radius-lg, 14px);

  --reg-transition:          var(--blue-inst-transition, all .25s cubic-bezier(.4,0,.2,1));

  /* Si tens un set de sombras institucionales, usalo; si no, queda fallback */
  --reg-shadow-lg:           var(--blue-inst-shadow-lg,
                                0 10px 15px -3px rgba(0,0,0,.1),
                                0 4px 6px -2px rgba(0,0,0,.05));

  /* ===== alias modal (se mantienen igual pero ahora toman azul por reg-*) ===== */


  --mi-bg:                   #fff;
  --mi-bg-soft:              var(--blue-inst-gray-100);  /* antes #fafbff */
  --mi-border:               var(--reg-gray-200);
  --mi-text:                 var(--reg-dark);
  --mi-muted:                var(--reg-gray-600);
  --mi-shadow:               var(--reg-shadow-lg);
  --mi-radius-lg:            var(--reg-border-radius-lg);
  --mi-radius-md:            var(--reg-border-radius);

  /* Densidad compacta y tamaos (se conservan) */
  --mi-space-xxs:            4px;
  --mi-space-xs:             6px;
  --mi-space-sm:             10px;
  --mi-space-md:             12px;
  --mi-space-lg:             14px;

  --mi-fs-xs:                .8rem;
  --mi-fs-sm:                .88rem;
  --mi-fs-md:                .95rem;
  --mi-fs-lg:                1.15rem;
}

/* ====== Overlay ====== */
.mi-modal__overlay{
  position:fixed; inset:0;
  background:rgba(15,23,42,.55);
  backdrop-filter:blur(2px);
  display:flex; align-items:center; justify-content:center;
  z-index:1000; animation:mi-fade .15s ease-out;
}

/* ====== Contenedor ====== */
.mi-modal__container{
  width:min(880px,94vw);
  max-height:88vh;
  background:var(--mi-bg);
  border-radius:var(--mi-radius-lg);
  box-shadow:var(--mi-shadow);
  display:flex; flex-direction:column;
  overflow:hidden; animation:mi-slide .2s ease-out;
  border:1px solid rgba(0,0,0,.04);
}

/* ====== Header (ROJO) ====== */
.mi-modal__header{
  display:flex; align-items:center; justify-content:space-between;
  padding:var(--mi-space-lg) var(--mi-space-lg);
  background:var(--mi-primary); color:#fff;
}
.mi-modal__title{ margin:0; font-size:var(--mi-fs-lg); font-weight:800; letter-spacing:-.02em; }
.mi-modal__subtitle{ margin:var(--mi-space-xxs) 0 0; font-size:var(--mi-fs-sm); opacity:.95; font-weight:500; }
.mi-modal__head-left{ flex:1; }
.mi-modal__close{
  appearance:none; border:0; background:transparent; color:#fff;
  width:36px; height:36px; border-radius:999px; display:grid; place-items:center;
  cursor:pointer; transition:transform .15s ease, background .15s ease;
}
.mi-modal__close:hover{ background:rgba(255,255,255,.12); transform:rotate(90deg); }

/* ====== Tabs (idntico a ModalInfoMesas) ====== */
.mi-tabs{
  display:flex; gap:12px; padding:0 18px;
  background:#fff; border-bottom:1px solid var(--mi-border);
}
.mi-tab{
  position:relative; appearance:none; border:0; background:transparent;
  padding:14px 8px; font-size:.92rem; font-weight:600; color:var(--mi-muted);
  cursor:pointer; transition:var(--reg-transition);
}
.mi-tab:hover{ color:var(--mi-primary-600); }
.mi-tab.is-active{ color:var(--mi-primary-700); }
.mi-tab.is-active::after{
  content:""; position:absolute; left:0; right:0; bottom:-1px; height:3px;
  background:var(--mi-primary-600); border-radius:3px 3px 0 0;
}

/* ====== Contenido (scroll) ====== */
.mi-modal__content{
  flex:1 1 auto; min-height:0; overflow:auto; background:var(--mi-bg-soft);
}
/* Scrollbar */
.mi-modal__content::-webkit-scrollbar{ width:8px; }
.mi-modal__content::-webkit-scrollbar-track{ background:#f1f5f9; }
.mi-modal__content::-webkit-scrollbar-thumb{ background:#cbd5e1; border-radius:8px; border:2px solid #f1f5f9; }
.mi-modal__content::-webkit-scrollbar-thumb:hover{ background:#94a3b8; }

/* ====== Panel ====== */
.mi-tabpanel{ display:none; padding:16px; background:linear-gradient(180deg,#fff 0%,#fbfcff 100%); }
.mi-tabpanel.is-active{ display:block; }

/* ====== Grid / Cards ====== */
.mi-grid{
  display:grid; grid-template-columns:repeat(12,1fr);
  gap:12px;
}
.mi-card{
  grid-column:span 6; /* 2 columnas por fila en desktop mediano */
  background:#fff;
  border:1px solid var(--mi-border);
  border-radius:var(--mi-radius-md);
  padding:12px;
  box-shadow:0 2px 10px rgba(15,23,42,.03);
}
.mi-card--full{ grid-column:1 / -1; }

.mi-card__title{
  margin:0 0 8px; font-size:var(--mi-fs-md);
  font-weight:800; color:var(--mi-text); letter-spacing:-.01em;
  padding-bottom:6px; border-bottom:1px solid var(--mi-border);
}

/* ====== Filas (label / value) ====== */
.mi-row{
  display:flex; align-items:center; justify-content:space-between;
  border-bottom:1px dashed rgba(0,0,0,.06); gap:12px; padding:8px 0;
}
.mi-row:last-child{ border-bottom:0; }

.mi-label{ color:var(--mi-muted); font-weight:700; font-size:var(--mi-fs-sm); }
.mi-value{ color:var(--mi-text); font-weight:600; text-align:right;  font-size:var(--mi-fs-md); }

/* Estados */
.mi-value.is-ok{ color:var(--reg-success); }
.mi-value.is-pend{ color:var(--reg-warning); }

/* ====== Responsivo ====== */
@media (max-width: 1200px){
  .mi-card{ grid-column:span 12; }
}
@media (max-width: 640px){
  .mi-modal__container{ width:96vw; max-height:86vh; }
  .mi-modal__title{ font-size:1.05rem; }
  .mi-modal__subtitle{ font-size:.8rem; }
  .mi-tab{ white-space:nowrap; }
  .mi-row{ flex-direction:column; align-items:flex-start; }
  .mi-value{ text-align:left; max-width:100%; }
}

/* ====== Animaciones ====== */
@keyframes mi-fade{ from{opacity:0} to{opacity:1} }
@keyframes mi-slide{ from{opacity:0; transform:translateY(14px)} to{opacity:1; transform:translateY(0)} }
